import { useState, useEffect } from 'react'
import { Container, Row, Col } from 'reactstrap'
import LeaderBoardRefereeCard from '../../components/LeaderBoardRefereeCard'
import SubTitle from '../../components/SubTitle'

// ** firebase
import firebase from 'firebase/app'

const TopReferees = () => {
  const [topReferees, setTopReferees] = useState([])

  useEffect(() => {
    firebase.database().ref('/users')
      .orderByChild('usertype')
      .equalTo('1') // All referees
      .once('value')
      .then((snapshot) => {
        const _referees = Object.values(snapshot.val())
          .map(item => ({ ...item, score: item.thumbsUp - item.thumbsDown }))

        // Set sorted referees
        setTopReferees(_referees.sort((a, b) => b.score - a.score).slice(0, 12))
      })
  }, [])

  return (
    <>
      <SubTitle title={"top referees"} />
      <Container className='px-5'>
        <Row className='px-5'>
          {topReferees.map((user, index) => (
            <Col sm={4} md={4} lg={3} xl={2} key={index}>
              <LeaderBoardRefereeCard {...user} />
            </Col>
          ))}
        </Row>
      </Container>
    </>
  )
}

export default TopReferees
