import { Card, CardBody, CardFooter, CardImg } from 'reactstrap'
import classnames from 'classnames'

export default function LeaderBoardRefereeCard({avatarURL, username, score, isOnline}) {

  return (
    <Card className='p-0 px-1 pt-1'>
      <CardImg top src={avatarURL} alt="Card image cap" className='rounded-bottom' />
      <CardBody className='p-0 my-1'>
        <p className='h3 text-info'><strong>{username}</strong></p>
        <strong>Score:</strong>{' '}{score}
      </CardBody>
      <CardFooter className={classnames('p-1 pb-0 text-uppercase text-center', {
        'text-success': isOnline,
        'text-muted': !isOnline
      })}><strong>{isOnline ? 'online' : 'offline'}</strong></CardFooter>
    </Card>
  )
}
